body {
  margin: 0;
  background: #23252c;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  font-family: Inter;
}
* {
  box-sizing: border-box;
}
a {
  text-decoration: none;
}
.kill {
  border-radius: 20px;
  max-width: 500px;
  color: white;
  background: #1a1b21;
}
.container {
  padding: 0 30px;
}
.header {
  text-align: center;
  &__power {
    border-radius: 20px 20px 0px 0px;
    width: 500px;
    height: 300px;
    object-fit: cover;
  }
  &__title {
    font-weight: bold;
    font-size: 30px;
    line-height: 30px;
    margin: 0;
    margin-top: 21px;
  }
  &__subtitle {
    margin: 0;
    font-size: 20px;
    margin-top: 15px;
    line-height: 15px;
    color: #f3bf99;
  }
  &__link {
    display: block;
    color: white;
    font-size: 17px;
    margin-top: 15px;
  }
  &__contact {
    display: block;
    padding: 10px 0;
    background: white;
    color: black;
    margin-top: 20px;
    border-radius: 5px;
    font-size: 22px;
    & > img {
      margin-right: 10px;
    }
  }
}
.email {
  width: 20px;
}
.footer {
  background: #161619;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px 0;
  border-radius: 0px 0px 20px 20px;
  & > a > img {
    width: 40px;
  }
}
.main {
  margin-bottom: 30px;
  &__title {
    font-size: 25px;
  }
  &__text {
    font-size: 10.24px;
    line-height: 150%;
    font-size: 18px;
  }
}
